type IconType = keyof typeof IconsData;
type IconPath = typeof IconsData[IconType];

const Icon = ({ icon, ...props }: (React.SVGProps<SVGSVGElement> & { icon: IconType })) => {
  const path: IconPath = IconsData[icon]
  return (
    <svg {...props}>
      <use xlinkHref={`${icons}#${path}`} />
    </svg>
  );
};

type IconProxyType = typeof Icon & Record<IconType, (props: React.SVGProps<SVGSVGElement>) => JSX.Element>

const IconProxy: IconProxyType = new Proxy<IconProxyType>(Icon as IconProxyType, {
  get(target, prop: IconType, receiver) {
    if (IconsData[prop]) {
      return (props: React.SVGProps<SVGSVGElement>) => <Icon icon={prop as IconType} {...props} />
    } else {
      return Reflect.get(target, prop, receiver);
    }
  }
})


export default IconProxy;