import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Home, NotFound } from './pages';
import { useLocalStorage, useMedia } from 'react-use';
import { ThemeContext, MapaProvider } from './context';
import { ITheme } from './context/ThemeContext';

const useTheme: () => [ITheme['setTheme'], ITheme['theme'], ITheme['source']] = () => {
	const usingDarkMode = useMedia('(prefers-color-scheme: dark)');
	const [themeSource, setThemeSource] = useLocalStorage('theme', 'dark');
	const theme = themeSource === 'system' ? (usingDarkMode ? 'dark' : 'light') : themeSource;
	return [setThemeSource as ITheme['setTheme'], theme as ITheme['theme'], themeSource as ITheme['source']];
}

const App = () => {
	const [setTheme, theme, source] = useTheme();

	return (
		<ThemeContext.Provider
			value={{
				theme,
				setTheme,
				source
			}}
		>
			<MapaProvider>
				<Router>
					<Switch>
						<Route component={Home} path="/" exact />
						<Route component={NotFound} path="*" />
					</Switch>
				</Router>
			</MapaProvider>
		</ThemeContext.Provider>
	);
};

export default App;
